/* RESET CSS */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
}
button,
input[type="button"],
input[type="submit"],
input[type="reset"],
input[type="file"] {
  border-radius: 0;
}
input[type="text"]::-ms-clear {
  display: none;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
sup {
  vertical-align: super;
}
sub {
  vertical-align: sub;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
* {
  box-sizing: border-box;
}





body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
  Arial, sans-serif;
}
main {
  /* background: yellow; */
  max-width: 935px;
  margin: 0 auto;
  padding: 30px 20px 0 20px;
}
@media only screen and (max-width: 735px) {
  main {
    padding: 0;
  }
}
.search-icon {
  background-image: url("/src/assets/images/icons.png");
  background-size: 440px 411px;
  background-position: -428px -241px;
  width: 10px;
  height: 10px;
  display: block;
}
.arrow-right-icon {
  background-image: url("/src/assets/images/icons.png");
  background-size: 440px 411px;
  background-position: -402px -207px;
  width: 24px;
  height: 24px;
  transform: rotate(180deg);
}
.media-icon {
  background-image: url("/src/assets/images/icons-2.png");
  background-size: 65px 65px;
  background-position: 0 0;
  display: block;
  width: 32px;
  height: 32px;
}

button, .button {
  all: unset;
  font-weight: 600;
  font-size: 14px;
  color: #0095f6;
  line-height: 18px;
  padding: 5px 9px;
  border-radius: 4px;
  cursor: pointer;
}
.primary {
  background: #0095f6;
  color: white;
  -webkit-text-fill-color: white;
}
.verified-icon {
  background-image: url("/src/assets/images/icons.png");
  background-position: -194px -351px;
  background-size: 440px 411px;
  width: 18px;
  height: 18px;
  display: block;
}

/* Utils */
.mobile-only {
  display: none;
}
@media only screen and (max-width: 735px) {
  .mobile-only {
    display: block;
  }
}
@media only screen and (max-width: 735px) {
  .desktop-only {
    display: none;
  }
}











/* Navigation */
nav .logo {
  height: 29px;
  /* border: 1px solid green; */
}
.search-guide .search-placeholder {
  color: hsl(0, 0%, 56%);
  font-size: 14px;
  margin-left: 6px;
}
.search-guide {
  width: 215px;
  height: 28px;
  overflow: hidden;
  white-space: nowrap;
  background: hsl(0, 0%, 98%);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 6px 26px;
  border-radius: 3px;
  border: 1px solid hsl(0, 0%, 86%);
}
.nav-content {
  padding: 0 15px;
  max-width: 935px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  height: 54px;
  align-items: center;
}
@media only screen and (max-width: 735px) {
  .nav-content {
    padding: 0 20px;
  }
}
nav .fixed {
  background: white;
  position: fixed;
  z-index: 1;
  width: 100%;
  border-bottom: 1px solid hsl(0, 0%, 86%);
}
nav .mock {
  height: 54px;
}

/* Header */
header {
  margin-bottom: 44px;
}
@media only screen and (max-width: 735px) {
  header {
    margin-bottom: 0;
  }
}
.header-grid {
  display: grid;
  grid-template-columns: 1fr 2fr;
}
@media only screen and (max-width: 735px) {
  .header-grid {
    display: flex;
    padding: 14px;
  }
}
.header-grid .profile-pic {
  height: 160px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header-grid .profile-pic img {
  width: 150px;
  height: 150px;
  border-radius: 1000px;
}
@media only screen and (max-width: 735px) {
  .header-grid .profile-pic {
    width: 77px;
    height: 77px;
    margin-right: 28px;
  }
  .header-grid .profile-pic img {
    width: 100%;
    height: 100%;
  }
}
.profile-info .title h2 {
  font-size: 28px;
  font-weight: 300;
}
.profile-info .title {
  display: flex;
  align-items: center;
}
.profile-info .title span {
  margin-left: 8px;
}
.profile-info .title .button {
  margin-left: 20px;
}
@media only screen and (max-width: 735px) {
  .profile-info .title {
    display: block;
  }
  .profile-info .title h2 {
    display: inline-block;
    margin-bottom: 12px;
  }
  .profile-info .title span {
    display: inline-block;
  }
  .profile-info .title .button {
    display: block;
    margin-left: 0;
  }
}
.profile-info .details li {
  font-size: 16px;
  font-weight: 400;
  margin-right: 40px;
}
.profile-info .details li span {
  font-weight: 600;
}
.profile-info .details ul {
  display: flex;
}
.profile-info .description h1 {
  font-weight: 600;
  line-height: 24px;
}
.profile-info .description span {
  font-weight: 400;
  line-height: 24px;
}
.profile-info .description a {
  color: hsl(209, 100%, 21%);
}
@media only screen and (max-width: 735px) {
  .profile-info .description h1 {
    line-height: 20px;
  }
  .profile-info .description span {
    line-height: 20px;
  }
  .profile-info .description {
    font-size: 14px;
    padding-left: 16px;
    padding-bottom: 21px;
    margin-bottom: 0 !important;
  }
}
.profile-info .row {
  margin-bottom: 20px;
}
.profile-info .row.last {
  margin-bottom: 0;
}

/* Story */
.story-item {
  /* background: green; */
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 15px;
  cursor: pointer;
}
@media only screen and (max-width: 735px) {
  .story-item {
    padding: 0 5px;
  }
}
.story-item .image img {
  width: 77px;
  height: 77px;
  object-fit: cover;
  border-radius: 1000px;
  display: block;
}
@media only screen and (max-width: 735px) {
  .story-item .image img {
    width: 56px;
    height: 56px;
  }
}
.story-item .image {
  border: 1px solid hsl(0, 0%, 86%);
  border-radius: 1000px;
  padding: 3px;
}
.story-item .title {
  font-size: 14px;
  font-weight: 600;
  padding-top: 15px;
  padding-bottom: 5px;
  /* background: pink; */
  text-align: center;
  white-space: nowrap;
  width: 80px;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media only screen and (max-width: 735px) {
  .story-item .title {
    font-size: 12px;
    font-weight: 400;
    width: 65px;
    padding-top: 8px;
  }
}
.stories {
  display: flex;
  padding: 0 24px;
  position: relative;
  margin-bottom: 44px;
  overflow: auto;
}
@media only screen and (max-width: 735px) {
  .stories {
    padding: 0;
    margin-bottom: 21px;
  }
}
.slider-button-next {
  position: sticky;
  right: 0;
  display: flex;
  align-items: center;
}